.img-white {
  filter: invert(48%) sepia(13%) saturate(3207%) hue-rotate(130deg) brightness(95%) contrast(80%);
}

.card {
  margin-top: 170px;
  margin-left: 50px;
  width: 300px;
  height: 150%;
}

.menu-items {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.divider {
  width: 10px;
  height: auto;
  display: inline-block;
}

.link-divider {
  width: 70px;
  height: auto;
  display: inline-block;
}

.divider2 {
  width: 2px;
  height: auto;
  display: inline-block;
}

.divider3 {
  width: 13px;
  height: auto;
  display: inline-block;
}

.divider4 {
  width: 20px;
  height: auto;
  display: inline-block;
}

.linkedin-divider {
  width: 17px;
  height: auto;
  display: inline-block;
}

.links {
  margin-top: 65px;
  position: fixed;
  top: 0;
  width: 100%;
  height: 5%;
  display: flex;
  background-color: #75736e;
  padding: 20px;
}

.dark-mode-button {
  margin-left: auto;
  margin-right: 45px;
}

.name {
  font-size: 25px;
  padding-left: 15px;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body.light-mode {
  background-color: #f2f0eb;
  color: #333;
  transition: background-color 0.3s ease;
  .links {
    background-color: #e8e6e1;
  }
}

body.dark-mode {
  background-color: #151e26;
  color: #dfdfdf;
  transition: background-color 0.3s ease;

  .navbar {
    background-color: #1b2938;
  }

  .links {
    background-color: #17302e;
  }

  .dark-mode-toggle > button {
    color: #999;
    &:last-child {
      color: lightblue;
    }
  }
}

.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  background-color: #73aad1;
  padding: 20px;
}

.content {
  padding: 20px;
  margin: 0 auto;
  margin-top: 60px;
  max-width: 600px;
  font-size: 20px;
  line-height: 1.3em;

  h1 {
    font-size: 2rem;
  }

  img {
    width: 100%;
  }

  code {
    background-color: #cbe9ff;
    border-radius: 2px;
    font-size: 1em;
    padding: 0 5px;
  }

  a {
    text-decoration: none;
    color: #006688;
  }

  button {
    color: #006688;
    background-color: transparent;
    border: none;
    font-size: 1em;
    padding: 0;
  }
}

body.dark-mode .content code {
  background-color: #1b2938;
}

body.dark-mode .content a {
  color: #ee09;
}

body.dark-mode .content button {
  color: #ee09;
}

.dark-mode-toggle {
  display: flex;
  margin: 0 auto;
  & > button {
    font-size: 1.2em;
    background: none;
    border: none;
    color: #ffe600;
    cursor: pointer;
    transition: color 0.3s ease;
    &:last-child {
      color: #666;
    }

    &:focus {
      outline: none;
    }
  }
}

.toggle-control {
  position: relative;
  padding: 0 4px;
  display: flex;
  align-items: center;
}

input[type='checkbox'].dmcheck {
  width: 40px;
  height: 10px;
  background: #555;
  position: relative;
  border-radius: 5px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  vertical-align: 2px;
  outline: none;

  &:checked + label {
    left: 30px;
  }

  &:focus-visible {
    outline: solid 2px white;
  }

  & + label {
    display: inline-block;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    transition: all 0.3s ease;
    cursor: pointer;
    position: absolute;
    left: 2px;
    background: #fff;
    opacity: 0.9;
    background-color: #f6f6f6;
  }
}
